<template>
    <div class="reset-container">
        <el-form autoComplete="on" label-position="left"
                 label-width="0px"
                 class="card-box reset-form">
            <div>
                <router-link to="/business-credit" class="back-icon">
                    首页<i class="el-icon-arrow-right"></i>
                </router-link>
                <h3 class="title">业务信用分</h3>
            </div>
            <router-link :to="opUrl" >
            <el-button type="primary" style="width:80px;">
                新增业务
            </el-button>
            </router-link>
          <el-table
            :data="basicList"
            style="width: 100%">
            <el-table-column
              prop="busName"
              label="业务"
              width="180">
            </el-table-column>
            <el-table-column
              prop="busId"
              label="业务id "
              width="180">
            </el-table-column>
            <el-table-column
              prop="memo"
              label="备注">
            </el-table-column>
             <el-table-column
              prop="updateTime"
              label="创建时间">
            </el-table-column>
             <el-table-column
              prop="status"
              label="当前状态">
            </el-table-column>       
            <el-table-column label="操作" align="center" width="160">
                <template scope="scope">
                <el-button @click="to(scope.row.link)" v-bind:disabled="!scope.row.link" type="text" size="small">查看详情</el-button>
                <el-button @click="editItem(scope.row.busName,scope.row.busId,scope.row.memo,scope.row.link)" type="text" size="small">编辑</el-button>
                </template>
</el-table-column>
</el-table>
<div class="block">
    <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="currPage" :page-sizes="[10, 20, 50, 100]" :page-size="pSize" layout="sizes, prev, pager, next, jumper" :total="totalNum">
    </el-pagination>
</div>
</el-form>
</div>
</template>

<script>
    import {
        listBusiness
    } from 'api/basicCredit';

    export default {
        name: 'reset',
        data() {
            return {
                currPage: 1,
                pSize: 10,
                basicList: [],
                currList: [],
                totalNum: 0,
                opUrl: "/credit-edit"
            }
        },
        computed: {},
        created() {
            this.changeCredit(this.currPage, this.pSize);
        },
        methods: {
            changeCredit(page, size) {
                var _vue = this;
                listBusiness(page, size).then(response => {
                    if (response.success) {
                        const data = response.data;
                        _vue.totalNum = response.recordsTotal;
                        data.forEach(function(e, i) {
                            e.status = e.status == 0 ? "正常" : "下架"
                        });
                        _vue.basicList = data;
                    }
                    resolve();
                }).catch(error => {
                    // reject(error);
                });
            },
            to(url) {
                    this.$router.push(url);
            },
            editItem(busName, busId, memo, link) {
                this.$router.push('/credit-edit?busName=' + busName + "&busId=" + busId + "&memo=" + memo + "&link=" + link);
            },
            handleSizeChange(val) {
                this.pSize = val;
                this.changeCredit(this.currPage, this.pSize);
            },
            handleCurrentChange(val) {
                this.currPage = val;
                this.changeCredit(this.currPage, this.pSize);
            }
        }
    }
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
    @import "src/styles/mixin.scss";
    .el-pagination {
        text-align: right;
    }
    
    .reset-container {
        .el-table {
            margin-top: 20px;
            margin-bottom: 20px;
        }
        input:-webkit-autofill {
            -webkit-box-shadow: 0 0 0px 1000px #293444 inset !important;
            -webkit-text-fill-color: #3E3E3E !important;
        }
        @include relative;
        height: 100vh;
        background-color: #fff;
        .back-icon {
            float: left;
            margin-top: 5px;
        }
        .el-icon-information {
            position: absolute;
            right: -18px;
            top: 10px;
        }
        .reset-form {
            position: absolute;
            left: 0;
            right: 0;
            width: 350px;
            padding: 35px 35px 15px 35px;
        }
        .card-box {
            padding: 20px;
            box-shadow: 0 0px 8px 0 rgba(0, 0, 0, 0.06), 0 1px 0px 0 rgba(0, 0, 0, 0.02);
            -webkit-border-radius: 5px;
            border-radius: 5px;
            -moz-border-radius: 5px;
            background-clip: padding-box;
            margin-bottom: 20px;
            background-color: #F9FAFC;
            width: 100%;
        }
        .title {
            margin: 0px auto 40px auto;
            text-align: left;
            color: #505458;
        }
    }
</style>


